#menu_control {
  position: absolute;
  z-index: -2;
  opacity: 0; }

#menu_control:checked ~ .header > .container > .nav {
  top: 100%; }

.header {
  background-color: #AA0601;
  position: relative; }
  .header .container {
    display: flex;
    justify-content: space-between; }
    .header .container .logo a {
      display: block;
      padding: 16px 0px 15.43px 0px; }
    .header .container .logo img {
      width: 80px;
      height: 48.57px; }
  .header .nav {
    display: flex;
    gap: 64px; }
    @media (max-width: 767px) {
      .header .nav {
        flex-direction: column;
        gap: 0px;
        right: 0;
        background-color: #AA0601;
        display: block;
        text-align: center;
        width: 100%;
        position: absolute;
        top: -500px; } }
    .header .nav li {
      padding: 25px 0px;
      font-size: 20px; }
      .header .nav li a {
        color: #fff;
        text-decoration: none; }
        @media (max-width: 767px) {
          .header .nav li a {
            display: block; } }
  .header .menu_btn {
    width: 30px;
    height: 20px;
    background: #AA0601;
    display: block;
    position: absolute;
    top: 30px;
    right: 20px;
    display: none;
    cursor: pointer; }
    .header .menu_btn::before {
      content: '';
      position: absolute;
      width: 30px;
      height: 2px;
      top: 0px;
      bottom: 0px;
      margin: auto;
      background-color: #fff;
      box-shadow: 0 10px 0 #fff, 0 -10px 0 #fff; }
    .header .menu_btn span {
      opacity: 0;
      width: 1px;
      height: 1px;
      display: block;
      overflow: hidden; }
    @media (max-width: 767px) {
      .header .menu_btn {
        display: block; } }

